  max-height: 19rem;
  opacity: 1 !important;
}

.dropdown-content-deck li>a, .dropdown-content-deck li>span {
  color: black !important;
  pointer-events: none;
  cursor: none;
}

.word-list-title{
  text-transform: capitalize;
  font-weight: bold;
}

.dropdown-content-deck li {
  cursor: auto;
  opacity: 1px;
  pointer-events: none !important;
}

.dropdown-content-deck:hover {
  cursor: auto !important;
}

@media screen and (max-width: 450px) {
  .info-dropdown{
    display: none !important;
  }
}


  .flip-card-front, .flip-card-back {
    border-radius: 15px;
    border: lightgray;
    background-color: white;
    position: absolute;
    width: 100%;
    height: 100%;  
    -webkit-backface-visibility: hidden;  
            backface-visibility: hidden;
    z-index: 2;
  }

  .flip-card-front{
    -webkit-justify-content: center;
            justify-content: center;
    font-size: 1200%;
  }
 
  .flip-card-back {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
    -webkit-justify-content: center;
            justify-content: center;
    z-index: 1;
    padding: 20px;
  }

  .mnemdiv{
    max-height: 5rem;
    overflow: scroll;
    text-align: left;
    width: 100%;
  }

  .mnemdiv::-webkit-scrollbar { 
    display: none; 
  }
  
  .flip-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: -webkit-transform 0.6s;
    transition: transform 0.6s;
    transition: transform 0.6s, -webkit-transform 0.6s;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
  }
  
  .flip-card:hover .flip-card-inner {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
  }
  
  .editmnembut:hover{
    cursor: pointer;
  }

  .addmnembut:hover{
    cursor: pointer;
  }

  .back-h{
    font-style: italic;
  }

  .back-kan{
    font-size: 5rem;
  }

  .back-eng{
    font-size: 3rem;
  }

  .hard-btn, .easy-btn{
    border-radius: 7px;
    border: lightgray;
    margin-top: 4%;
    width: 47%;
    background-color: white;
    color: black;
    box-shadow: none;
  }

  .hard-btn{
    margin-right: 3%;
  }

  .easy-btn{
    margin-left: 3%;
  }

  .row, .col{
    margin-bottom: 0px;
  }

  .test-row{
    margin: -5px;
  }
  
