.info-content{
    max-width: 52rem;
  min-width: 20rem;
  padding: 5px;
  color:black;
  margin-left: auto;
  margin-right: auto;
  margin-top: 2rem;
}

.info {
    background: rgba(255, 255, 255, 0.9);
    border-style: none;
    border-radius: 15px;
    padding: 40px;
    margin-bottom: 1.5rem;
    height: auto;

}

.howto-row {
    margin-top: 1rem;
    margin-bottom: 1rem;
}

.fcimg{
    max-width: 10rem;
}

#beginlink{
    color: white;
    font-size: 2rem;
}

@media only screen and (max-width: 600px) {
    .strokeorderimg{
        border-radius: 15px;
        max-width: 16rem;
    }
    
    .fruitimg, .logoimg{
        min-width: 15rem;
        max-width: 16rem;
    }
  }

  @media only screen and (min-width: 600px) {
    .strokeorderimg{
        border-radius: 15px;
        max-width: 20rem;
    }
    
    .fruitimg, .logoimg{
        max-width: 21rem;
    }
  }