.pfc-btn{
  color: black;
  background-color: #ffffff;
  border-radius: 5px;
  border-color: white;
  margin: 0.5rem;
  width: 8rem;
  height: 6.5rem;
  font-size: 4rem;
}

.pf-content{
  margin-top: 2rem;
}

.pfc-btn:hover{
background-color: #ededed;
cursor: pointer;
border-color: none;
}

.pfc-card{
  padding: 1rem;
  min-width: 20rem;
  max-width: 25rem;
  height: 38rem;
  background-color: white;
  margin: auto;
  border-radius: 15px;
}

.back-btn{
margin: 0.5rem;
}

.back-buttons{
position: absolute;
right:    0;
bottom:   0;
margin-bottom: 2rem;
}

.back-h{    
font-style: italic;
}

.pback-kan{    
font-size: 7rem;
}

.pback-eng{
font-size: 3rem;
}

img {
margin-top: 1rem;
max-width: 22rem;
height: auto;
}

.pmnemdiv{
margin-left: 0.5rem;
width: 100%;
}

.pmnemtext{
max-height: 8rem;
overflow: scroll;
text-align: left;
}

.pmnemtext::-webkit-scrollbar { 
display: none; 
}

@media only screen and (min-width: 300px) and (max-width: 350px) {
  img {
    margin-top: 1rem;
    max-width: 19rem;
    height: auto;
  }
}
